}

.react-flow__node.node-line-reply,
.react-flow__node.node-line-push,
.react-flow__node.node-line-carousel {
  background: linear-gradient(135deg, #166534, #22c55e) !important;
  border-color: #4ade80 !important;
  color: white !important;
}

.react-flow__node.node-line-reply:hover,
.react-flow__node.node-line-push:hover,
.react-flow__node.node-line-carousel:hover {
  border-color: #ffffff !important;
}

.react-flow__node.node-line-reply:hover::before,
.react-flow__node.node-line-push:hover::before,
.react-flow__node.node-line-carousel:hover::before {
  width: 24px !important;
