  top: -12px !important;
  right: -12px !important;
}

.react-flow__node.node-data-map {
  background: linear-gradient(135deg, #0e7490, #06b6d4) !important;
  border-color: #22d3ee !important;
  color: white !important;
}

.react-flow__node.node-data-map:hover {
  border-color: #ffffff !important;
}

.react-flow__node.node-data-map:hover::before {
  width: 24px !important;
  height: 24px !important;
  top: -12px !important;
  right: -12px !important;
}

.react-flow__node.node-notification {
  background: linear-gradient(135deg, #a16207, #eab308) !important;
  border-color: #facc15 !important;
  color: white !important;
}

.react-flow__node.node-notification:hover {
  border-color: #ffffff !important;
}

.react-flow__node.node-notification:hover::before {
  width: 24px !important;
  height: 24px !important;
  top: -12px !important;
  right: -12px !important;
}

.react-flow__node.node-webhook-trigger {
  background: linear-gradient(135deg, #7c2d92, #a855f7) !important;
  border-color: #c084fc !important;
  color: white !important;
}

.react-flow__node.node-webhook-trigger:hover {
